/* App.css */

body {
  font-family: 'Press Start 2P', cursive;
  background-color: #121212; /* Dark background */
  color: #00ffcc; /* Neon green text */
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App {
  text-align: center;
  padding: 10%;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 255, 204, 0.4);
  background: linear-gradient(45deg, #1a1a1a, #333333);
  width: 90%;
  max-width: 400px;
  margin: 0 auto;
  border: 6px solid #00ffcc;
  animation: borderAnimation 2s infinite alternate;
}

@keyframes borderAnimation {
  0% {
    border-color: #00ffcc;
  }
  100% {
    border-color: #1a1a1a;
  }
}

button {
  font-size: 5vw; /* Responsive font size */
  padding: 8% 12%; /* Responsive padding */
  margin: 5% 0;
  cursor: pointer;
  border: 4px solid #00ffcc;
  border-radius: 12px;
  background: linear-gradient(45deg, #1a1a1a, #333333);
  color: #00ffcc;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1);
}

button:hover {
  background: linear-gradient(45deg, #333333, #1a1a1a);
  transform: scale(1.05);
}

.counts-container {
  margin-top: 5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

table {
  width: 48%;
  border-collapse: collapse;
  text-align: left;
  margin-bottom: 5%;
}

th, td {
  padding: 4%;
  border-bottom: 1px solid #00ffcc;
  font-size: 3vw; /* Responsive font size */
  color: #00ffcc;
}

.reset-button {
  font-size: 5vw; /* Responsive font size */
  padding: 8% 12%; /* Responsive padding */
  margin-top: 5%;
  cursor: pointer;
  border: 4px solid #00ffcc;
  border-radius: 12px;
  background: linear-gradient(45deg, #1a1a1a, #333333);
  color: #00ffcc;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1);
}

.reset-button:hover {
  background: linear-gradient(45deg, #333333, #1a1a1a);
  transform: scale(1.05);
}

.total-rolls {
  margin-top: 5%;
  font-size: 4vw; /* Responsive font size */
  color: #00ffcc;
}

.last-rolled {
  margin-top: 3%;
  font-size: 3vw; /* Responsive font size */
  color: #00ffcc;
}

h1 {
  color: #00ffcc;
  font-size: 5vw; /* Responsive font size */
  margin-bottom: 5%;
}
